import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NbToastrService } from '@nebular/theme';
import { isObject } from 'rxjs/internal/util/isObject';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { PackingUnitsServices } from '../../../services/packing-units/PackingUnits.services';


@Component({
  selector: 'create-edit-packing-unit',
  templateUrl: 'create-edit.component.html',
  styleUrls: ['create-edit.component.scss'],
})
export class CreateEditomponent implements OnInit {
  @ViewChild('autoComplete') autoComplete: ElementRef;
  formGroup: FormGroup;
  title: string;
  public dropzoneConfig: any = {
    paramName: 'file', // The name that will be used to transfer the file
    maxFilesize: 2, // MB
    autoQueue: false,
    url: '/',
    autoProcessQueue: false,
    multi: false
  };

  id: number;
  colorList: any[] = [];
  isLoading: boolean = false;
  autoLoad: boolean = false;
  selectedImage: File;

  constructor(
    private activeRouter: ActivatedRoute,
    private toaster: NbToastrService,
    private packingUnitsService: PackingUnitsServices,
    private route: Router) {
    this.formGroup = new FormGroup({
      id: new FormControl(),
      name_en: new FormControl(null, [Validators.required]),
      name_ar: new FormControl(null, [Validators.required]),
    })
  }

  ngOnInit() {
    this.activeRouter.params.subscribe(v => this.id = v.id);
    if (this.id) {
      this.title = 'Edit';
      this.getObject();
    } else {
      this.title = 'Add';
    }
  }

  getObject() {
    this.packingUnitsService.getPackingUnit(this.id).subscribe(
      res => {
        this.formGroup.patchValue(res['data']);
      },
      err => {
        this.toaster.show(err.error.message, 'Error', { status: 'danger' });
      },
      () => {
        // this.autoComplete.nativeElement.blur();
      });
  }

  editForm() {
    if (this.formGroup.valid) {
      this.isLoading = true;
      const uploadData = new FormData();
      this.formGroup.value.id ? uploadData.append('id', this.formGroup.value.id) : false;
      this.formGroup.value.name_en ? uploadData.append('name_en', this.formGroup.value.name_en) : false;
      this.formGroup.value.name_ar ? uploadData.append('name_ar', this.formGroup.value.name_ar) : false;
      
      this.packingUnitsService[(this.id ? 'edit' : 'Create') + 'PackingUnit'](uploadData).subscribe(
        res => {
          this.toaster.show(res['msg'], 'Success', { status: 'success' });
          this.route.navigateByUrl('/packing-units');
        },
        err => {
          this.toaster.show(err.error.message, 'Error', { status: 'danger' });
          this.isLoading = false;
        },
        () => {
          this.isLoading = false;
        }
      );

    } else {
      const controls: any = this.formGroup.controls
      let result: any[] = Object.keys(controls).map(key => ({ id: key, option: controls[key] }));
      result = result.filter(ele => ele.option.hasError('required'))
      this.toaster.show(result[0].id + ' is required', 'Error', { status: 'danger' });
    }
  }

  viewHandle(value: any) {
    if (isObject(value)) {
      return value.name_en
    } else {
      return value
    }
  }


  backToIndex() {
    this.route.navigate(['packing-units'])
  }
  get bf() {
    return this.formGroup.controls;
  }
}
